import { use } from 'react'
import ClientPage from './ClientPage'

export default function Page({ params }: { params: Promise<{ slug: string[] }> }) {
  const { slug } = use(params)
  const id = slug?.[0] ?? 'N/A'
  const title = slug?.[1] ?? 'N/A'

  return <ClientPage id={id} title={title} />
}
